<template>
  <div class="skill" id="skill"></div>
</template>

<script>
// 
let echarts = require("echarts/lib/echarts");
export default {
  name: "skill",
   data() {
    return {};
  },
  mounted() {
    this.drawECharts();
  },
  methods: {
    drawECharts() {
      // domecharts
      let myChart = echarts.init(document.getElementById("skill"));
      myChart.setOption({
        // 
        title: {
          // 
          text: "",
          // 
          textStyle: {
            color: "#333"
          },
          // 
          x: "center"
        },
        // 
        tooltip: {
          trigger: "item",
          // 
          formatter: "{a} <br/>{b} : {d}%"
        },
        // 
        legend: {
          x: "center",
          y: "bottom",
          textStyle: {
            color: "#333"
          },
          data: [ "HTML5", "CSS3", "JavaScript", "jQuery", "Scss/Sass","Vue", "", "" ]
        },
        // 
        calculable: true,
        // 
        series: [
          {
            name: "",
            type: "pie",
            radius: [30, 110],
            roseType: "area",
            data: [
              { value: 15, name: "HTML5" },
              { value: 15, name: "CSS3" },
              { value: 10, name: "JavaScript" },
              { value: 20, name: "jQuery" },
              { value: 15, name: "Scss/Sass" },
              { value: 20, name: "Vue" },
              { value: 20, name: "" },
              { value: 15, name: "" }
            ]
          }
        ],
      });
    }
  }
};
</script>

<style scoped>
.skill {
  width: 100%;
  height: 500px;
}
</style>


